.header {
    grid-area: h;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    font-size: 30px;
    background-color: rgba(0, 0, 0, 0.6);
    padding-left: 50px;
}
    @media(max-width: 600px) {
        .header {
            font-size: 30px;
            -ms-flex-pack: center;
                justify-content: center;
            padding: 0;
        }
    }

.header>a {
    text-decoration: none;
    color: white;
    margin: 25px;
    -webkit-transition: text-shadow 1s, -webkit-transform 1s;
    transition: text-shadow 1s, -webkit-transform 1s;
    -o-transition: transform 1s, text-shadow 1s;
    transition: transform 1s, text-shadow 1s;
    transition: transform 1s, text-shadow 1s, -webkit-transform 1s;
}

.header>a:hover {
    -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
            transform: scale(1.3);
    text-shadow: 3px 3px black;
}


